import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'

let Styleselect=()=>import('@/components/Styleselect')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        title: '智能锁 指纹锁 微信锁-Ola欧乐智能锁官网'
      }
    },
    {
      path: '/styleselect',
      name: 'Styleselect',
      component: Styleselect,
      meta: {
        title: '智能锁 指纹锁 微信锁-Ola欧乐智能锁官网'
      }
    }
  ]
})



