<template>
    <div>
        <!-- 标题 -->
        <div class="title">
            <van-nav-bar title="选择专属你的户门"/>
        </div>
        <!-- 内容 -->
        <div class="content">
            <!-- 步骤条 -->
            <div class="stepbar">
                <van-steps :active="active">
                    <van-step>款式选择</van-step>
                    <van-step>锁体选择</van-step>
                    <van-step>选择户门</van-step>
                    <van-step>效果查看</van-step>
                </van-steps>
            </div>
            <!-- 图片展示 -->
            <div class="show_image">
                <img src="../../static/0.jpg" alt="" ref="Bigimg">
            </div>
            <!-- 选择部分 -->
            <div class="choose">
                <div class="style_select">
                    <p>款式选择</p>
                    <ul>
                        <li v-for="(item, index) in Stylelist" :key="index" @click.prevent="styleAdd(index,$event)" :class="[inde === index ? 'active': '']" id="Smallimg"> 
                            <img :src="item.img" alt="" ref="one"> {{item.name}} 
                        </li>
                    </ul>
                </div>
                <!-- 锁芯选择 -->
                <div class="lock_select style_select">
                    <p>锁芯选择</p>
                    <p v-show="LockHide" style="color: #a2a1a1;font-size: 12px;margin-left: 20px;">请选择款式</p>
                    <ul v-show="LockShow">
                        <li v-for="(item,index) in LockList" :key="index" :class="[imke === index ? 'active': '']" @click="LockAdd(index)"> 
                            <img :src="item.img" alt="">  {{item.name}} 
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <!-- 底部 -->
        <div class="footer">
            <button :class="[dis === 0 ? 'disabled': '']" @click.prevent="Jump">查看效果图</button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            Stylelist:[
                {img: "./static/0.jpg", name: "双色款"},
                {img: "./static/1.jpg", name: "基础款"},
                {img: "./static/2.jpg", name: "华丽款"},
                {img: "./static/3.jpg", name: "奢华款"},
                {img: "./static/0.jpg", name: "极致款"}
            ],
            LockList: [
                {img: 'http://www.olalocks.com/images/ola_plus_20.png', name: '普通款'},
                {img: 'http://www.olalocks.com/images/ola_plus_21.png', name: '霸王款'}
            ],
            inde: 6,                    // 款式样式当前索引
            imke: 6,                    // 锁芯样式当前索引
            dis: 0,                     //
            active: 2,                  // 顶部步骤条当前索引
            LockShow: false,            // 锁芯选择显示隐藏
            LockHide: true,             // 锁芯选择显示隐藏
        }
    },
    methods: {
        styleAdd: function(id,event){
            this.inde = id
            
            // 获取当前点击图片的路径地址
            let imgs = event.path[0].childNodes[0].src

            if(this.inde == id){
                this.LockShow = true
                this.LockHide = false
                this.$refs.Bigimg.src = imgs
            }else{
                this.LockShow = false
                this.LockHide = true
            }
        },
        LockAdd(id){
            this.imke = id
        },
        Jump(){
            if(this.dis == 1){
                this.$router.push('/styleselect')
            }
        }
    },
    updated() {
        for (let index = 0; index < 5; index++) {
            if(this.inde == index && this.imke == 0 || this.imke == 1){
                this.dis = 1
            }
        }
    },
}
</script>

<style lang="less" scoped>
.title{
    width: 100%;
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #ccc;
    display: flex;
    position: fixed;
    top: 0;
    z-index: 22;
    background: #fff;
    .van-nav-bar{
        width: 100%;
    }
}
.content{
    margin-top: 51px;
    .stepbar{
        width: 90%;
        margin: 5px auto;
    }
    .show_image{
        img{
            width: 50%;
            margin: 0 auto;
            display: block;
            padding: 10px;
            background: rgb(250,250,248);
        }
    }
    .choose{
        margin-top: 15%;
        .active{
            // border: 1px solid rgb(7, 193, 96);
            color: rgb(7, 193, 96);
        }
        .style_select{
            padding-left: 5%;
            width: 90%;
            p{
                margin-bottom: 10px;
                font-weight: bold;
            }
            ul{
                width: 100%;
                height: 100%;
                display: inline-block;
                li{
                    font-size: 13px;
                    padding: 5px 0 5px 10px;
                    background: #f8f8f8;
                    width: 30%;
                    border-radius: 8px;
                    display: flex;
                    line-height: 21px;
                    float: left;
                    margin: 2% 10% 0 6%;
                    border: 1px solid #ccc;
                    img{
                        width: 20px;
                        height: 20px;
                        background-size: 100% 100%;
                        margin-right: 10px;
                    }
                }
            }
        }
        .lock_select{
            margin-top: 20px;
            margin-bottom: 86px;
            li{
                i{
                    background: url(http://www.olalocks.com/images/ola_plus_20.png);
                }
            }
        }
    }
}
.footer{
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 10px;
    background: #ededed;
    button{
        width: 90%;
        display: block;
        color: #fff;
        font-size: 18px;
        background: rgb(7, 193, 96);
        text-align: center;
        line-height: 35px;
        border-radius: 20px;
        margin: 0 auto;
    }
    .disabled{
        color: #cccccced;
    }
}
</style>


